html,body {
  background-color: rgb(205, 248, 234);
  margin: 0px;
  padding: 0px;
}
th,tr{
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
}
table,th,td {
  border: 1px solid black;
  border-collapse: collapse;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes chartIn {
    from {
        position: absolute;
        left: -100%;
    }

    to {
        position: absolute;
        left: 25.5%;
    }
}

@keyframes chartIn {
    from {
        position: absolute;
        left: -100%;
    }

    to {
        position: absolute;
        left: 25.5%;
    }
}

@-webkit-keyframes chartOut {
    from {
        position: absolute;
        left: 25.5%;
    }

    to {
        position: absolute;
        left: -100%;
    }
}

@keyframes chartOut {
    from {
        position: absolute;
        left: 25.5%;
    }

    to {
        position: absolute;
        left: -100%;
    }
}

.openChartBTN {
    height: 56px;
    width: 100px;
    border-radius: 20px;
    color: rgb(3, 2, 2);
    background-color: rgb(68, 156, 108);
    position: fixed;
    left: 2%;
    bottom: 3%;
    transition: ease-in 0.4s;
    font-size: 28px;
    font-weight: 800;
    text-align: center;
    z-index: 3;
}

.openChartBTN:hover {
    box-shadow: inset 150px 0px 50px 0px rgb(133, 223, 200);
    cursor: pointer;
}

.chartDel {
    position: absolute;
    left: -100%;
    top:10%;
    height: 600px;
    width: 1000px;
    -webkit-animation-name: chartOut;
            animation-name: chartOut;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    margin: 0px auto;
    padding-left: 40px;
    padding-bottom: 20px;
    border-top: 1px solid #ccc;
    background: linear-gradient(to top, #ccc 0, #ccc 1px, transparent 1px);
    background-size: 100% 30px;
    background-color: white;
    z-index: 2;
}
.chartDel::after {
    content: '\A   200000 \A   160000 \A   120000 \A   80000 \A   40000 \A   0 \A   -40000 \A   --80000 \A   -120000 \A   -160000 \A   -200000 ';
    line-height: 60px;
    white-space: pre;
    position: absolute;
    font-size: 12px;
    top: -85px;
    left: 0rem;
    text-align: right;
}

.nonDisplay {
    display: none;
}

.chart {
    -webkit-animation-name: chartIn;
            animation-name: chartIn;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    position: absolute;
    margin: 0px auto;
    top:10%;
    padding-left: 40px;
    padding-bottom: 20px;
    height: 600px;
    width: 1000px;
    border-top: 1px solid #ccc;
    background: linear-gradient(to top, #ccc 0, #ccc 1px, transparent 1px);
    background-size: 100% 30px;
    background-color: white;
    z-index: 2;
}

.chart::after {
    content: '\A   200000 \A   160000 \A   120000 \A   80000 \A   40000 \A   0 \A   -40000 \A   --80000 \A   -120000 \A   -160000 \A   -200000 ';
    line-height: 60px;
    white-space: pre;
    position: absolute;
    font-size: 12px;
    top: -85px;
    left: 0rem;
    text-align: right;
}

.result-bg {
    background: rgba(241, 241, 241, 0.5);
    display: inline-block;
    position: relative;
    width: 70px;
    height: 100%;
    margin-left: 20px;
    
}

.result-bg::after {
    content: attr(data-year)'\5E74';
    font-size: 12px;
    position: absolute;
    bottom: -1rem;
    left: 0;
    right: 0;
}

.result-bar {
    width: 100%;
    position: absolute;
    bottom: 50%;
    left: 0;
}
.result-bar2 {
    width: 100%;
    background: #db7568;
    position: absolute;
    left: 0;
}
  
